<template>
  <view style="position: relative;">
	  
	  
	  <el-breadcrumb separator-class="el-icon-arrow-right">
	    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
	    <el-breadcrumb-item>任务管理</el-breadcrumb-item>
	  </el-breadcrumb>
	  
	  <el-row :gutter="15" style='height: 80rpx; line-height: 70rpx;'>
	    <el-col :span="1.2"><div class="grid-content el-col-title-action">任务名：</div></el-col>
	    <el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="taskName"/></div></el-col>
		<el-col :span="1.2"><div class="grid-content el-col-title-action">等级：</div></el-col>
		<el-col :span="4">
			<div class="grid-content el-col-text-action">
				<el-select v-model="levelvalue" placeholder="请选择">
				    <el-option
				      v-for="item in leveloptions"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				</el-select>
			</div>
		</el-col>
		<el-col :span="1.2"><div class="grid-content el-col-title-action">地点：</div></el-col>
		<el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="taskPlace"/></div></el-col>
		<el-col :span="1.2"><div class="grid-content el-col-title-action">人数：</div></el-col>
		<el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="taskNum"/></div></el-col>
		<!-- <el-col :span="1"><div class="grid-content bg-purple el-col-title-action"></div></el-col> -->
		<el-col :span="3"><el-button round @click='getfilterdata' style='color: #56585a; background-color: #ffffff; width: 150rpx; height: 70rpx; line-height: 25rpx; text-align: center; padding: 0;'>搜索</el-button></el-col>
	  </el-row>
	  
	  <el-table
	    :data="tableData"
		:show-header='false'
	    style="width: 100%">
		<el-table-column
		  label="序号"
		  width="160">
		  <template slot-scope="scope">
		    <i class="el-icon-collection-tag"></i>
		    <span style="margin-left: 10px">{{ scope.row.id}}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="任务名"
		  width="200">
		  <template slot-scope="scope">
		    <i class="el-icon-data-analysis"></i>
		    <span style="margin-left: 10px">{{ scope.row.TaskName }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="搜救等级"
		  width="120">
		  <template slot-scope="scope">
		    <i class="el-icon-medal"></i>
		    <span style="margin-left: 10px">{{ scope.row.RescueLevel }}</span>
		  </template>
		</el-table-column>
	    <el-table-column
	      label="失踪人"
	      width="160">
	      <template slot-scope="scope">
			  
	        <el-popover trigger="hover" placement="top">
	          <p>姓名: {{ scope.row.RescuedName }}</p>
	          <!-- <p>住址: {{ scope.row.address }}</p> -->
	          <div slot="reference" class="name-wrapper">
	            <el-tag size="medium">{{ scope.row.RescuedName }}</el-tag>
	          </div>
	        </el-popover>
	      </template>
	    </el-table-column>
		<el-table-column
		  label="失踪省份"
		  width="160">
		  <template slot-scope="scope">
		    <i class="el-icon-position"></i>
		    <span style="margin-left: 10px">{{ scope.row.RescuedPlace }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="群人数"
		  width="80">
		  <template slot-scope="scope">
		    <i class="el-icon-pie-chart"></i>
		    <span style="margin-left: 10px">{{ scope.row.Sum }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="创建日期"
		  width="300">
		  <template slot-scope="scope">
		    <i class="el-icon-time"></i>
		    <span style="margin-left: 10px">{{ scope.row.RescuedTime | format_time }}</span>
		  </template>
		</el-table-column>
	    <el-table-column label="操作">
	      <template slot-scope="scope">
	        <el-button
	          size="mini"
	          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
			<el-button
			  size="mini"
			  @click="ceshi(scope.$index, scope.row)">成员</el-button>
	      </template>
	    </el-table-column>
		<el-table-column label="删除">
		  <template slot-scope="scope">
		    <el-button
		      size="mini"
		      type="danger"
			  @click="getuserId"
		      >删除</el-button>
			<el-dialog
			  title="提示"
			  :visible.sync="dialogVisible"
			  width="30%">
			  <span>请确认是否删除此任务？</span>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="handleDelete(scope.$index, scope.row)">确 定</el-button>
			  </span>
			</el-dialog>
		  </template>
		</el-table-column>
	  </el-table>
	 <view style="padding-top: 30px;">
		 <el-pagination
		   background
		   layout="pager"
		   @current-change="handleCurrentChange"
		   :total="datanum">
		 </el-pagination>
	 </view>
  </view>
</template>

<script>
	import moment from 'moment'
  export default {
    data() {
      return {
		dialogVisible: false,
        tableData: [],
		datanum: '',
		taskname: '',
		taskgrade: '',
		taskplace: '',
		tasknum: '',
		leveloptions: [{
		  value: 'S',
		  label: 'S'
		}, {
		  value: 'A',
		  label: 'A'
		}, {
		  value: 'B',
		  label: 'B'
		}, {
		  value: 'C',
		  label: 'C'
		}, {
		  value: 'D',
		  label: 'D'
		}],
		levelvalue: ''
	  }
    },
	mounted() {
		this.getdata(1)
	},
    methods: {
	  // 监听输入
	  taskName: function(res) {
		  this.taskname = res.detail.value
	  },
	  taskGrade: function(res) {
	  	  this.taskgrade = res.detail.value
	  },
	  taskPlace: function(res) {
	  	  this.taskplace = res.detail.value
	  },
	  taskNum: function(res) {
	  	  this.tasknum = res.detail.value
	  },
	  
      handleEdit(index, row) {
        console.log(index, row.id);
		this.$emit('openpopup', {'title':'编辑任务', 'showflagarr':[1,4,1,3], 'id':row.id, 'keyname': ['任务名：', '搜救等级：', '被搜救人：', '日期：']})
      },
      handleDelete(index, row) {  // 删除一个任务
		this.dialogVisible = false;
		// this.delgroup(row.id)
      },
	  handleCurrentChange(index){ // 按页查询数据
		  console.log(index)
		  this.getdata(index)
	  },
	  handleClose(done) {   // 关闭提示
	    this.$confirm('确认关闭？')
	      .then(_ => {
	        done();
	      })
	      .catch(_ => {});
	  },
	  getdata(index) {	  // 初次请求数据（群组）
		  try {
		  	this.token = uni.getStorageSync('token')
		  	this.AdmId = uni.getStorageSync('AdmId')
		  } catch(e) {
		  }
		  uni.request({
		  	method: 'GET',
			url: this.$store.state.url + 'tasksadmin/',
			header: {
				'Content-Type' : 'application/json',
				'Authorization': 'JWT ' + this.token,
				'AdmId': this.AdmId
			},
			data: {
				page: index,
			},
			success: (ret) => {
				this.datanum = ret.data.count
				this.tableData = ret.data.results
			}
		  })
	  },
	  // 筛选信息
	  getfilterdata() {
		uni.request({
			url: 'http://124.70.75.88:8888/tasksadmin/taskcondition/',
			method: 'POST',
			header: {
				'Content-Type' : 'application/json',
				'Authorization': 'JWT ' + this.token,
				'AdmId': this.AdmId
			},
			data: {
				TaskName: this.taskname,
				RescueLevel: this.levelvalue,
				RescuedPlace: this.taskplace
			},
			success: (res) => {
				this.tableData = res.data
				console.log(this.tableData)
			}
		})
	  },
	  getuserId(index, row) {
	  		  this.dialogVisible = true
	  		  this.uid = row.id
	  },
	  delgroup(id) {   // 删除一个任务
		 uni.request({
		 	method: 'DELETE',
		 	url: this.$store.state.url + 'tasksadmin/' + id,
		 	success: (ret) => {
		 		console.log('删除成功')
		 	}
		 })
	  },
	  
	  ceshi(index, row) {
		// 对父组件的传值
		this.$emit('getIndex1', [5,row.id])
	  },
    },
	filters: {
	  format_str(id) {  // id格式过滤器
	  	  var strId = String(id)
		  var len = 7 - strId.length
		  var em = '0'
		  for(var i=1; i<len; i++) {
		  	em+='0'
		  }
		  
	  	  return em + strId
	  },
	  format_coordinate(lat, lng) {  // 坐标过滤器
		  lat = parseFloat(lat).toFixed(2)
		  lng = parseFloat(lng).toFixed(2)
		  return '( ' + lat + ', ' + lng + ')'
	  },
	  format_time(time) {   // 时间过滤器
		 return moment(new Date(time)).format('YYYY-MM-DD HH:mm:ss')
	  }
	}
  }
</script>

<style>
	/deep/ .el-table td, .el-table th.is-leaf{
		padding: 20px 25px;
	}
	.el-breadcrumb{
		margin-bottom: 20px;
		font-size: 18px;
	}
	/deep/ .el-pagination{
		height: 10px;
	}
	
	/* 搜索栏样式 */
	.el-row {
	    margin-bottom: 20px;
	    &:last-child {
	      margin-bottom: 0;
	    }
	  }
	  .el-col {
	    border-radius: 4px;
	  }
	  .bg-purple-dark {
	    background: #ece7eb;
	  }
	  .bg-purple-light {
	    background: #ece7eb;
	  }
	  .grid-content {
	    border-radius: 4px;
	    min-height: 36px;
	  }
	  .row-bg {
	    padding: 10px 0;
	    background-color: #f9fafc;
	  }
	  
	  .el-col-title-action {
		  background-color: #e9eef3;
	  }
	  .el-col-text-action {
		  min-width: 200rpx;
		  border-radius: 20rpx;
	  }
	  .el-col-button-action {
		  width: 150rpx;
		  border-radius: 20rpx;
	  }
	  .el-col-input-action {
		  height: 70rpx;
		  border-radius: 20rpx;
		  color: #56585a;
		  background-color: #ffffff;
	  }
	  
	  
	  
</style>